const uni = require('uni').default;
































import {formatDateYMD} from '../../../common/util.js';
//let formatDateYMD = require('../../../common/util.js').formatDateYMD;
export default {
	data() {
		return {
			nowDate: formatDateYMD(new Date())
		}
	},
	methods:{
		onTapGoLookClockDetails(){
			uni.showToast({
				title:'我的打卡记录',
				mask:true,
				duration:1500
			})
		},
		onClickGoClock(){
			uni.showToast({
				title:'去打卡',
				mask:true,
				duration:1500
			})
		},
		onClickStudyAgain(){
			uni.showToast({
				title:'再学一会',
				mask:true,
				duration:1500
			})
		}
	}
}



